import { useState } from 'react';
import { Link } from "react-router-dom";

const CategoryItem = ({props}) => {
    <div className='container'>
        <div>
        <a href="#">
        <img src={props.img} title="category" alt="category image"></img>
        </a>
        </div>
        <div>
            <h1 className='font-satisfy text-center text-slate text-7xl'>{props.name}</h1>
        </div>
    </div>

}

export default CategoryItem;